.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-self: center;
  height: 40px;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: grid;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__title {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rates {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__rates-reviews {
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  margin-top: 16px;
}

.page {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 2px;
}

.stars .stars__star:first-child {
  margin-left: 0;
}

.stars--1 > .stars__star:nth-child(-n+1), .stars--2 > .stars__star:nth-child(-n+2), .stars--3 > .stars__star:nth-child(-n+3), .stars--4 > .stars__star:nth-child(-n+4), .stars--5 > .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.button:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.main-content {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main-content {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .main-content {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .main-content {
    --columns: 4;
  }
}

.nav {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  text-align: center;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

